<template>
  <header>
    <div id="header" class="mx-auto">
      <div
        class="relative mx-auto sm:mx-auto min-h-screen hero-background bg-fixed "
      >
        <nav id="nav" :class="stickyNavBar()" class="justify-between">
          <a
            href="#header"
            class="inline-block sm:ml-56 my-auto "
            v-smooth-scroll
          >
            <Icons name="logo"></Icons>
          </a>

          <div class="text-right inline-block  my-auto sm:text-3xl text-sm">
            <a
              href="#about"
              v-smooth-scroll
              class="sm:mr-20 mr-10 hover:text-gray-600"
              >About me</a
            >
            <a
              href="#contact"
              v-smooth-scroll
              class="sm:pr-20 pr-8 hover:text-gray-600"
              >Contact</a
            >
          </div>
        </nav>
        <div class="sm:w-1/2 absolute sm:right-0 ml-16">
          <h1
            data-aos="fade-left"
            data-aos-delay="50"
            data-aos-duration="3000"
            class="text-center sm:text-6xl text-4xl pr-40 mt-40 sm:max-w-3xl text-gray-800 font-semibold"
          >
            Because programming is more than <br />
            job...
          </h1>
        </div>
      </div>
    </div>
  </header>
</template>
 
<script>
export default {
  data() {
    return {
      active: false,
      scrollPosition: null
    };
  },
  methods: {
    stickyNavBar() {
      if (this.scrollPosition > 0) {
        this.active = true;
        return "sticky-nav";
      } else {
        this.active = false;
        return "nav";
      }
    },
    updateScrollPosition() {
      this.scrollPosition = window.scrollY;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.updateScrollPosition);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hero-background {
  background-image: url("~@/assets/hero.jpg");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

nav.sticky-nav {
  @apply transition-all duration-500 pl-4 flex  bg-white bg-opacity-50 shadow-xl h-24 fixed w-full z-50;
}

nav.nav {
  @apply transition-all duration-500 pl-4 flex shadow-2xl w-full h-32 z-50;
}
.text-middle {
  @apply;
}
</style>
