<template>
  <div>
    <div
      id="select"
      class="w-72 mx-auto  mt-10 border-black border-4 rounded-lg border-opacity-75 cursor-pointer"
    >
      <div
        class="flex selected-option text-2xl font-bold justify-between px-4"
        @click="isShown = !isShown"
        ref="dropdown"
      >
        <p class="p-2">
          {{ selectedOption.name }}
          <i class=" pl-3" :class="selectedOption.icon"></i>
        </p>
        <i
          class="fas fa-sort-up fa-lg transition-all duration-200 my-auto pt-3"
          :class="{ rotate: isShown }"
        ></i>
      </div>
      <transition name="fade">
        <div class="options-box mt-4 text-center" v-if="isShown">
          <div
            class="option border-b-2 border-black last:border-b-0 first:border-t-2
           font-semibold transition duration-300 ease-in-out hover:bg-black hover:text-white"
            v-for="option in options"
            :key="option.id"
            @click="select()"
          >
            <input
              type="radio"
              name="contact"
              :id="option.id"
              :value="option"
              v-model="selectedOption"
            />
            <label :for="option.id">
              {{ option.name }}
              <i class="pl-3" :class="option.icon"></i>
            </label>
          </div>
        </div>
      </transition>
    </div>
    <contact-link
      :selectedOption="selectedOption"
      :options="options"
    ></contact-link>
  </div>
</template>
<script>
import contactLink from "./contact-link";
export default {
  components: {
    "contact-link": contactLink
  },
  data() {
    return {
      selectedOption: {
        id: "default",
        name: "Choose an option..",
        content: "",
        class: "",
        icon: ""
      },
      options: [
        {
          id: "mail",
          name: "My e-mail",
          content: "mailto: adba1999@gmail.com",
          class:
            " hover:bg-red-600 hover:text-white border-red-600 text-red-600",
          icon: "far fa-envelope text-red-600 fa-lg"
        },
        {
          id: "facebook",
          name: "My facebook",
          content: "https://www.facebook.com/adrian.bazela.5",
          class:
            "hover:bg-indigo-600 hover:text-white border-indigo-600 text-indigo-600",
          icon: "fab fa-facebook text-indigo-600 fa-lg"
        },
        {
          id: "github",
          name: "My github",
          content: "https://github.com/adbal99",
          class: "hover:bg-black hover:text-white border-black",
          icon: "fab fa-github fa-lg"
        }
      ],
      isShown: false
    };
  },

  // click outside part
  created() {
    window.addEventListener("click", this.close);
  },

  beforeDestroy() {
    window.removeEventListener("click", this.close);
  },

  methods: {
    select() {
      this.isShown = false;
    },

    close(e) {
      if (!this.$refs.dropdown.contains(e.target)) {
        this.isShown = false;
      }
    }
  }
};
</script>
<style scoped>
input[type="radio"] {
  display: none;
}

.options-box label {
  cursor: pointer;
  width: 23.5rem;
  height: 2rem;
  display: block;
}
.options-box .option {
  font-size: 1.5rem;
  padding-top: 10px;
  padding-bottom: 10px;
}
.rotate {
  @apply transform rotate-180;
}

/*
you set the css property when transition ends
*/
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}

/*
you set the css property before transition starts
*/
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>